//    Icon
.icon {
  display: inline-block;
  vertical-align: middle;
  flex-shrink: 0;
  object-fit: contain;
  user-select: none;
  pointer-events: none;
  
  //     
  filter: brightness(0) saturate(100%) invert(0);
  transition: all 0.2s ease;
  
  // 
  &.icon-small {
    width: 24px;
    height: 24px;
    min-width: 24px;
    min-height: 24px;
  }
  
  &.icon-medium {
    width: 32px;
    height: 32px;
    min-width: 32px;
    min-height: 32px;
  }
  
  &.icon-large {
    width: 40px;
    height: 40px;
    min-width: 40px;
    min-height: 40px;
  }
  
  &.icon-xlarge {
    width: 48px;
    height: 48px;
    min-width: 48px;
    min-height: 48px;
  }
  
  //    
  .dark-mode & {
    filter: brightness(0) saturate(100%) invert(1);
    opacity: 0.9;
  }
  
  //     
  button &,
  .btn &,
  .icon-btn & {
    pointer-events: none;
  }
  
  // Hover     
  button:hover &,
  .btn:hover &,
  .icon-btn:hover & {
    transform: scale(1.1);
    opacity: 1;
  }
}

//     
.stat-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  
  .icon {
    width: 100%;
    height: 100%;
  }
}





